import Password from '@/components/password/password.vue'
import BlocInvisible from '@/components/blocInvisible/blocInvisible.vue'

export default {
    name: "EscalierIndex",
    components: {
        BlocInvisible,
        Password
    },
    data() {
        return {
            urlImage: require("@/assets/images/escaliers.jpg"),
            song: new Audio(require('@/assets/musics/escalier.mp3')),
            showDame: false,
            coords: []
        }
    },
    
    created() {
        this.song.play();
    },

    unmouted() {
        this.song.pause();
    },
    methods: {
        passwordFound() {
            // On fait tout disparaitre
            let password = document.getElementById("grosseDameModale");
            password.style.animation = "disparition 5s forwards";
            password = document.getElementById("imgGrosseDame");
            password.style.animation = "disparition 5s forwards";
            password = document.getElementsByClassName("fondModal")[0];
            password.style.animation = "disparition 5s forwards";

            let background = document.getElementById("imgEscalier");
            setTimeout(() => {
                background.classList.add("zoomIn")
            }, 2000);
            
            // 12 sec
            setTimeout(() => {
                this.$router.push('/chambre');
            }, 12000);

            // La musique
            this.decreaseSong();
        },

        decreaseSong() {
            let timer = setInterval(() => {
                if (this.song.volume - 0.1 < 0) {
                    clearInterval(timer);
                } else {
                    this.song.volume -= 0.1;
                }
            }, 1000)
        },

        clickImg(e) {
            var cursorLeft = e.layerX;
            var cursorTop = e.layerY;

            var heightBg = e.target.height;
            var widthBg = e.target.width;

            this.coords.push(
                {
                    left: cursorLeft,
                    top: cursorTop
                }
            );

            if (this.coords.length == 2) {

                let hauteur = (this.coords[1].top - this.coords[0].top) * 100 / heightBg;
                let largeur = (this.coords[1].left - this.coords[0].left) * 100 / widthBg;

                let left = this.coords[0].left * 100 / widthBg;
                let top = this.coords[0].top * 100 / heightBg;


                console.log(`hauteur: ${hauteur.toFixed(2)}\nlargeur: ${largeur.toFixed(2)}\nleft: ${left.toFixed(2)}\ntop: ${top.toFixed(2)}`)
                this.coords = [];
            }
        },

        resizeImage() {
            var image = document.getElementById("imgEscalier");
    
            if (image.offsetWidth > window.innerWidth) {
                image.style.width = "100%";
                image.style.height = "auto";
            
                var container = image.parentElement;
                container.style.width = "100%";        
                container.style.height = "max-content"; 
            }       
        }
    }
}